import React, {Component} from 'react';
import {Route, Switch} from 'react-router-dom';
import {Nav, NavItem, NavLink} from 'reactstrap';
import {NavLink as RRNavLink} from 'react-router-dom';
import Projects from './Projects';

class Portfolio extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isLoading: true,
            activeCategory: 'frontend',
            categories: [
                { id: 'frontend', name: 'Front-End', icon: '💻' },
                { id: 'nodejs', name: 'Node.JS', icon: '⚡' },
                { id: 'reactjs', name: 'React.JS', icon: '⚛️' },
                { id: 'flutter', name: 'Flutter', icon: '📱' },
                { id: 'android', name: 'Android', icon: '🤖' }
            ]
        };
    }

    componentDidMount() {
        // Simulate loading time for smooth transition
        setTimeout(() => {
            this.setState({ isLoading: false });
        }, 800);
    }

    handleCategoryChange = (categoryId) => {
        this.setState({ 
            activeCategory: categoryId,
            isLoading: true 
        });
        
        // Brief loading state for category switch
        setTimeout(() => {
            this.setState({ isLoading: false });
        }, 300);
    }

    render() {
        const { isLoading, activeCategory, categories } = this.state;

        return (
            <div className="container pb-5">
                <div className="row justify-content-md-center">
                    <div className="col-12 col-md-10">
                        <div className="container content-container pb-5">
                            <div className="row justify-content-md-center">
                                <div className="col-12 content-header d-flex flex-column justify-content-center">
                                    <h1 className="text-center">Portfolio</h1>
                                </div>
                            </div>
                            
                            {/* Portfolio Navigation with Loading States */}
                            <div className="portfolio-navigation-wrapper">
                                <div className="p-md-3 p-1 content-menu">
                                    <Nav pills className="d-flex flex-column flex-md-row content-menu">
                                        {categories.map((category) => (
                                            <NavItem 
                                                key={category.id} 
                                                className="content-menu text-center"
                                            >
                                                <NavLink
                                                    activeClassName="active"
                                                    to={`${this.props.match.url}/${category.id}`}
                                                    tag={RRNavLink}
                                                    onClick={() => this.handleCategoryChange(category.id)}
                                                    className={`portfolio-nav-link ${isLoading ? 'loading' : ''}`}
                                                >
                                                    <span className="category-icon">{category.icon}</span>
                                                    <span className="category-name">{category.name}</span>
                                                    {isLoading && activeCategory === category.id && (
                                                        <div className="loading-spinner"></div>
                                                    )}
                                                </NavLink>
                                            </NavItem>
                                        ))}
                                    </Nav>
                                </div>
                            </div>

                            {/* Portfolio Content with Loading Animation */}
                            <div className={`portfolio-content ${isLoading ? 'loading' : 'loaded'}`}>
                                <Route 
                                    path={`/portfolio/:category`} 
                                    render={(routeProps) => (
                                        <Projects {...routeProps} isLoading={isLoading} />
                                    )} 
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Portfolio;
