import React, {Component} from 'react';
import {connect} from "react-redux";
import {Link, withRouter} from "react-router-dom";
import {Button, ButtonGroup} from "reactstrap";
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faGithub} from '@fortawesome/free-brands-svg-icons';
import {faExternalLinkAlt} from '@fortawesome/free-solid-svg-icons';
import {BounceLoader} from "react-spinners";

class Projects extends Component {

    state = {
        totalLoaded: 0,
        loaded: false,
        category: this.props.match.params.category
    }

    loaded = () => {
        this.setState(prevState => ({
            totalLoaded: prevState.totalLoaded + 1,
        }));
    }

    setLoadedTo = (value) => {
        this.setState(prevState => ({
            loaded: value,
        }));
    }

    renderLiveViewButton(liveViewLink) {
        if(liveViewLink) {
            return (
                <Button 
                    tag="a" 
                    target="_blank" 
                    rel="noopener noreferrer" 
                    href={liveViewLink}
                    className="btn btn-outline-primary"
                >
                    <FontAwesomeIcon icon={faExternalLinkAlt} size="lg" className="me-2"/>
                    Live Demo
                </Button>
            );
        }
    }

    renderProjects() {
        const projects = this.props.items.map((item, index) => {
            return (
                <div key={item.id || index} className="project-card-wrapper">
                    <div className={`card project-card ${item.displayType}`}>
                        <div className="card-img-wrapper">
                            <img 
                                src={item.coverImage} 
                                alt={item.title}
                                className="card-img-top"
                                onLoad={this.loaded}
                            />
                            <div className="card-img-overlay">
                                <div className="overlay-content">
                                    <h5 className="card-title">{item.title}</h5>
                                    <div className="project-buttons">
                                        <ButtonGroup>
                                            <Button 
                                                tag="a" 
                                                target="_blank" 
                                                rel="noopener noreferrer" 
                                                href={item.gitHubLink}
                                                className="btn btn-outline-dark"
                                            >
                                                <FontAwesomeIcon icon={faGithub} size="lg" className="me-2"/>
                                                GitHub
                                            </Button>
                                            {this.renderLiveViewButton(item.liveView)}
                                            <Link 
                                                to={`/${this.props.match.params.category}/projectdetails/${item.id}`}
                                                className="btn btn-outline-info"
                                            >
                                                Details
                                            </Link>
                                        </ButtonGroup>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="card-body">
                            <h5 className="card-title">{item.title}</h5>
                            <p className="card-text">
                                {item.description || 'A showcase of my development skills and creativity.'}
                            </p>
                            <div className="project-tech-stack">
                                {item.technologies && item.technologies.map((tech, idx) => (
                                    <span key={idx} className="tech-badge">{tech}</span>
                                ))}
                            </div>
                        </div>
                    </div>
                </div>
            );
        });

        return (
            <div className="projects-grid">
                {projects}
            </div>
        );
    }

    renderLoader() {
        if (this.state.loaded) {
            return null;
        } else {
            if (this.state.totalLoaded < this.props.items.length) {
                return (
                    <div className='sweet-loading' style={{"padding":"100px","margin":"auto"}}>
                        <BounceLoader
                            sizeUnit={"px"}
                            size={60}
                            color={'#F0DC6C'}
                            loading={!this.state.loaded}
                        />
                        <p className="loading-text">
                            Loading {this.state.totalLoaded} of {this.props.items.length} projects...
                        </p>
                    </div>
                );
            } else {
                this.setLoadedTo(true);
                return null;
            }
        }
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        if(nextProps.category !== prevState.category) {
            return { loaded: false, totalLoaded: 0, category: nextProps.category };
        }
        else return null;
    }

    render() {
        const { isLoading } = this.props;
        
        return (
            <div className={`projects-container ${isLoading ? 'loading' : 'loaded'}`}>
                {this.renderLoader()}
                {this.renderProjects()}
            </div>
        );
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        items: state.portfolio[ownProps.match.params.category]
    }
}

export default withRouter(connect(mapStateToProps)(Projects));
